import Vue from "vue";
import VueRouter from "vue-router";
import Home from "../views/Home.vue";
import Index from "../views/Index.vue";
import About from "../views/About.vue";
import Clients from "../views/Clients.vue";
import Client from "../views/Client.vue";
import Contact from "../views/Contact.vue";
import Pos from "../views/Pos.vue";
import Mision from "../views/Mision.vue";
import Vision from "../views/Vision.vue";
import Values from "../views/Values.vue";
import History from "../views/History.vue";
import Products from "../views/Products.vue";
import Rep from "../views/Rep.vue";
import Blog from "../views/Blog.vue";
import Services from "../views/Services.vue";
import Solutions from "../views/Solutions.vue";
import Hardware from "../views/Hardware.vue";
import Error from "../views/Error.vue";
import Service from "../views/Service.vue";
import Solution from "../views/Solution.vue";
import Product from "../views/Product.vue";

Vue.use(VueRouter);

const routes = [
  {
    path: "/",
    name: "Home",
    component: Home
  },
  {
    path: "/blog",
    name: "blog",
    component: Blog
  },
  { path: "/:country/", redirect: "/:country/index" },
  {
    path: "/:country/index",
    name: "index",
    component: Index,
    props: true
  },
  {
    path: "/:country/contact",
    name: "contact",
    component: Contact,
    props: true
  },
  {
    path: "/:country/about",
    name: "about",
    component: About,
    props: true
  },
  {
    path: "/:country/clients",
    name: "clients",
    component: Clients,
    props: true
  },
  {
    path: "/:country/clients/:id",
    name: "client",
    component: Client,
    props: true
  },
  {
    path: "/:country/products/",
    name: "Products",
    component: Products,
    props: true
  },
  {
    path: "/:country/about/pos",
    name: "Pos",
    component: Pos,
    props: true
  },
  {
    path: "/:country/about/mision",
    name: "Mision",
    component: Mision,
    props: true
  },
  {
    path: "/:country/about/vision",
    name: "vision",
    component: Vision,
    props: true
  },
  {
    path: "/:country/about/values",
    name: "values",
    component: Values,
    props: true
  },
  {
    path: "/:country/about/history",
    name: "History",
    component: History,
    props: true
  },
  {
    path: "/:country/rep",
    name: "Rep",
    component: Rep,
    props: true
  },
  {
    path: "/:country/products/services",
    name: "Services",
    component: Services,
    props: true
  },
  {
    path: "/:country/products/services/:id",
    name: "Service",
    component: Service,
    props: true
  },
  {
    path: "/:country/products/solutions",
    name: "Solutions",
    component: Solutions,
    props: true
  },
  {
    path: "/:country/products/solutions/:id",
    name: "Solution",
    component: Solution,
    props: true
  },
  {
    path: "/:country/products/hardware/:id",
    name: "Product",
    component: Product,
    props: true
  },
  {
    path: "/:country/products/hardware",
    name: "Hardware",
    component: Hardware,
    props: true
  },
  {
    path: "*",
    name: "error",
    component: Error
  }
];

const router = new VueRouter({
  base: process.env.BASE_URL,
  routes
});

export default router;
