<template>
  <div>
    <b-navbar toggleable="lg" type="light" variant="light" fixed="top">
      <a class="navbar-brand" to="/">
        <img src="img\bfgp.png" width="30" height="30" alt="" />
        <span v-text="pais"></span>
      </a>

      <b-navbar-toggle target="nav-collapse"></b-navbar-toggle>

      <b-collapse id="nav-collapse" is-nav>
        <b-navbar-nav>
          <b-nav-item :href="links.inicio">Inicio</b-nav-item>
          <b-nav-item :href="links.blog">Blog</b-nav-item>
          <b-nav-item :href="links.about">Conocenos</b-nav-item>
          <b-nav-item :href="links.products">Productos</b-nav-item>
          <b-nav-item :href="links.rep">Representaciones</b-nav-item>
          <b-nav-item :href="links.clients">Clientes</b-nav-item>
          <b-nav-item :href="links.contact">ContÃ¡ctenos</b-nav-item>
        </b-navbar-nav>
      </b-collapse>
    </b-navbar>
  </div>
</template>

<script>
export default {
  props: ["name"],
  data() {
    return {
      pais: "",
      links: {
        inicio: "",
        about: "",
        products: "",
        rep: "",
        clients: "",
        contact: "",
        blog: "",
        error: ""
      }
    };
  },
  created() {
    this.updateLinks(this.name);
    this.updateName(this.name);
  },
  methods: {
    updateLinks(country) {
      this.links.inicio = "";
      this.links.about = "";
      this.links.products = "";
      this.links.rep = "";
      this.links.clients = "";
      this.links.contact = "";
      this.links.blog = "";
      this.links.inicio = "/bfpg-dist/#/" + country + "/index";
      this.links.about = "/bfpg-dist/#/" + country + "/about";
      this.links.products = "/bfpg-dist/#/" + country + "/products";
      this.links.rep = "/bfpg-dist/#/" + country + "/rep";
      this.links.clients = "/bfpg-dist/#/" + country + "/clients";
      this.links.contact = "/bfpg-dist/#/" + country + "/contact";
      this.links.blog = "/bfpg-dist/#/blog";
    },
    updateName(country) {
      if (country == "ve") this.pais = "Venezuela";
      else if (country == "co") this.pais = "Colombia";
      else if (country == "pa") this.pais = "PanamÃ¡";
      else this.error = "error de pais";
    }
  },
  watch: {
    name: function() {
      this.updateLinks(this.name);
      this.updateName(this.name);
    }
  }
};
</script>

