<template>
  <div>
    <b-container>
      <div class="divider div-transparent"></div>
      <b-row align-h="center">
        <b-col cols="12" md="1" align-self="center">
          <b-img src="img\bfgp.png" alt="logo de BFPG" height="100%" />
        </b-col>
        <b-col cols="12" md="3" align-self="center">
          <h1>Contacto</h1>
        </b-col>
      </b-row>
      <div class="divider div-transparent div-arrow-down"></div>
      <b-row>
        <b-col cols="6">
          <b-img src="https://placekitten.com/g/408/287"></b-img>
        </b-col>
        <b-col cols="6">
          <b-form @submit="onSubmit" @reset="onReset" v-if="show">
            <b-form-group
              id="input-group-1"
              label="Correo electrÃ³nico:"
              label-for="input-1"
            >
              <b-form-input
                id="input-1"
                v-model="form.email"
                type="email"
                required
                placeholder="Correo"
              ></b-form-input>
            </b-form-group>

            <b-form-group
              id="input-group-2"
              label="Nombre:"
              label-for="input-2"
            >
              <b-form-input
                id="input-2"
                v-model="form.name"
                required
                placeholder="Nombre"
              ></b-form-input>
            </b-form-group>
            <b-button type="submit" variant="primary">Enviar</b-button>
            <b-button type="reset" variant="danger">Limpiar</b-button>
          </b-form>
        </b-col>
      </b-row>
    </b-container>
  </div>
</template>

<script>
export default {
  data() {
    return {
      form: {
        email: "",
        name: ""
      },
      show: true
    };
  },
  methods: {
    onSubmit(evt) {
      evt.preventDefault();
      alert(JSON.stringify(this.form));
    },
    onReset(evt) {
      evt.preventDefault();
      // Reset our form values
      this.form.email = "";
      this.form.name = "";
      // Trick to reset/clear native browser form validation state
      this.show = false;
      this.$nextTick(() => {
        this.show = true;
      });
    }
  }
};
</script>

